import React, { useEffect, useState } from 'react';
import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import "react-responsive-carousel/lib/styles/carousel.min.css";
import { Carousel } from 'react-responsive-carousel';
import './Home.css';
import Container from '@material-ui/core/Container';
import Link from '@material-ui/core/Link';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';

function Copyright() {
    return (
      <Typography variant="body2" color="textSecondary">
        {'כל הזכויות שמורות לניתאי לאוקס - עיצוב אתרים ואפליקציות © '}
        <Link color="inherit" href="https://material-ui.com/">
          Koi
        </Link>{' '}
        {new Date().getFullYear()}
        {'.'}
      </Typography>
    );
  }

  function getBreakPoint(windowWidth) {
    if (windowWidth) {
      if (windowWidth < 600) {
        return "xs";
      } else if (windowWidth < 960) {
        return "sm";
      } else if (windowWidth < 1280) {
        return "md";
      } else if (windowWidth < 1920) {
        return "lg"
      } else {
        return "xl";
      }
    } else {
      return undefined;
    }
  }

const useStyles = makeStyles((theme) => ({
    root: {
      flexGrow: 1,
    },
    menuButton: {
      marginRight: theme.spacing(2),
    },
    title: {
      flexGrow: 1,
    },
    appbar: {
        alignItems: 'center',
      },
    main: {
        marginTop: theme.spacing(8),
        marginBottom: theme.spacing(2),
    },
    footer: {
        textAlign: 'center',
        padding: theme.spacing(3, 2),
        marginTop: 'auto',
        backgroundColor:
          theme.palette.type === 'light' ? theme.palette.grey[200] : theme.palette.grey[800],
    },
    menuPaper: {
        backgroundColor: "darkred"
      }
  }));

export default function Home() {
    const classes = useStyles();
    const isWindowClient = typeof window === "object";
    const [windowSize, setWindowSize] = useState(
      isWindowClient
        ? getBreakPoint(window.innerWidth) //👈
        : undefined
    );
    const [anchorEl, setAnchorEl] = React.useState(null);

    const handleClick = (event) => {
      setAnchorEl(event.currentTarget);
    };
    const handleClose = () => {
      setAnchorEl(null);
    };
  
  
  
    useEffect(() => {
      //a handler which will be called on change of the screen resize
      function setSize() {
        setWindowSize(getBreakPoint(window.innerWidth)); //👈
      }
  
      if (isWindowClient) {
        //register the window resize listener
        window.addEventListener("resize", setSize);
  
        //unregister the listerner on destroy of the hook
        return () => window.removeEventListener("resize", setSize);
      }
    }, [isWindowClient, setWindowSize]);
  
    useEffect(() => {
        document.getElementById('vid').play();
    }, [])

    
    return (
        <div style={{
            backgroundImage: "url(background2.jpg)",
            backgroundRepeat: 'no-repeat',
            backgroundAttachment: 'fixed',
            backgroundSize: 'cover',
            backgroundPosition: '50% 50%'
            }} 
            id="home">
            {windowSize !== 'xs' && 
            <Grid container spacing={0}>
                <AppBar className={classes.appbar} position="fixed" style={{backgroundColor: 'black', height: '150px', textAlign: 'center'}}>
                    <Toolbar>
                    <img src="whatsapp.png" className={classes.menuButton} style={{width: '40px', padding: '30px'}}/>
                    <img src="instagram.png" className={classes.menuButton} style={{width: '40px', padding: '30px'}}/>
                    <img src="facebook.png" className={classes.menuButton} style={{width: '40px', padding: '30px'}}/>
                    <Grid item lg={4} md={2}>
                        <Typography onClick={()=> document.getElementById('contact').scrollIntoView()}
                        variant="h6" style={{margin: '50px', lineHeight: '150px'}}
                        className="navItem"
                        >
                            צור קשר
                        </Typography>
                    </Grid>
                    <Grid item lg={1} md={1} sm={1}>
                        <Typography onClick={()=> document.getElementById('gallery').scrollIntoView({block: 'end'})} 
                        variant="h6" style={{margin: '50px', lineHeight: '150px'}}
                        className="navItem"
                        >
                            גלריה
                        </Typography>
                    </Grid>
                    <Grid item lg={5} md={2} sm={2}>
                        <Typography onClick={()=> document.getElementById('about').scrollIntoView({block: 'center'})} 
                        variant="h6" style={{margin: '50px', lineHeight: '150px'}}
                        className="navItem"
                        >
                            KOI אודות
                        </Typography>
                    </Grid>
                    <Grid item lg={4} md={2} sm={2}>
                        <Typography onClick={()=> document.getElementById('home').scrollIntoView({block: 'start'})}
                        variant="h6" style={{margin: '50px', lineHeight: '150px'}} className="navItem">
                            דף הבית
                        </Typography>
                    </Grid>
                    <Grid item lg={2} md={1} sm={1}>
                        <Typography variant="h6" style={{margin: '50px', lineHeight: '150px'}}>
                            0507742044
                        </Typography>
                    </Grid>
                    </Toolbar>
                </AppBar>
            </Grid>}
            {windowSize === 'xs' && 
            <AppBar position="fixed" style={{height: '20%', backgroundColor: 'black'}}>
                <Grid container direction="column" justify="center" alignItems="center" spacing={0} style={{ minHeight: '20vh' }}>
                    <Toolbar>
                        <Grid item xs={12}>
                            <a href="tel:+972507742044" style={{color: 'white', fontSize: '20px'}}>
                                חייג עכשיו: 0507742044
                            </a>
                        </Grid>
                            <Button aria-controls="simple-menu" aria-haspopup="true" onClick={handleClick} style={{position: 'absolute', right: '0', left: '230px', top: '14px', color: 'white'}}>
                                <MenuIcon />
                            </Button>
                        <Menu
                            id="simple-menu"
                            anchorEl={anchorEl}
                            keepMounted
                            open={Boolean(anchorEl)}
                            onClose={handleClose}
                            classes={{ paper: classes.menuPaper }}
                        >
                            <MenuItem 
                            onClick={() => document.getElementById('home').scrollIntoView({block: 'start'})} 
                            style={{color: 'white'}}>
                                דף הבית
                            </MenuItem>
                            <MenuItem 
                            onClick={() =>document.getElementById('about').scrollIntoView({block: 'center'})} 
                            style={{color: 'white'}}>
                                KOI אודות
                            </MenuItem>
                            <MenuItem 
                            onClick={() =>document.getElementById('gallery').scrollIntoView({block: 'center'})} 
                            style={{color: 'white'}}
                            >
                                גלריה
                            </MenuItem>
                            <MenuItem 
                            onClick={() => document.getElementById('contact').scrollIntoView()} 
                            style={{color: 'white'}}
                            >
                                צור קשר
                            </MenuItem>
                            <MenuItem onClick={handleClose} style={{color: 'white'}}>
                            סגור חלון
                            </MenuItem>
                        </Menu>
                        <img src="whatsapp.png" className={classes.menuButton} style={{width: '20px', padding: '30px', position: 'absolute', top: '30px',}}/>
                        <img src="instagram.png" className={classes.menuButton} style={{width: '20px', padding: '30px', position: 'absolute', top: '30px', left: '150px'}}/>
                        <img src="facebook.png" className={classes.menuButton} style={{width: '20px', padding: '30px', position: 'absolute', top: '30px', left: '80px'}}/>
                    </Toolbar>
                </Grid>
            </AppBar>}

            <div style={{width: '100%', height: '800px', backgroundColor: 'darkred'}}>
                <Grid
                container
                direction="column"
                justify="center"
                alignItems="center"
                spacing={0}
                >                
                    <Grid item sm={4}>
                        {windowSize !== 'xs' && <img src="logo.jpg" style={{width: '730px', position: 'relative', top: '100px', right: '130px', mixBlendMode: 'multiply'}}/>}
                    </Grid>
                </Grid>
            </div>
            {windowSize !== 'xs' && <div style={{maxWidth:'80%', height:'auto', display:'block', margin: '0 auto'}}>
                {windowSize === 'lg' && <h1 style={{color: 'lightgrey', position: 'absolute', fontSize: '60px', margin: 'auto', left: '30%', top: '1100px', opacity: '70%'}}>
                    KOI-ברוכים הבאים ל
                </h1>}
                {windowSize === 'xl' && <h1 style={{color: 'lightgrey', position: 'absolute', fontSize: '60px', margin: 'auto', left: '30%', top: '110px', opacity: '70%'}}>
                    KOI-ברוכים הבאים ל
                </h1>}
                <video width="100%" height="auto" loop="true" autoplay="autoplay" id="vid" muted>
                    <source src="koi.mp4" type="video/mp4" />
                </video>
            </div>}
            {windowSize === 'xs' && <div style={{maxWidth:'100%', height:'auto', display:'block', margin: '0 auto'}}>
                <video width="100%" height="auto" loop="true" autoplay="autoplay" id="vid" muted>
                    <source src="koi.mp4" type="video/mp4" />
                </video>
            </div>}
            <br />
            <br />
            <br />
            <div style={{textAlign: 'center'}} id="about">
                <h1 style={{color: 'white', fontFamily: 'Amatic SC cursive', fontSize: '50px', paddingLeft: '10px'}}>KOI אודות</h1>
                <h3 style={{color: 'white'}}>.הינו מתחם חדש לאירועים בפתח תקווה הצופה בקיר פנורמי לאצטדיון המושבה KOI </h3>
                <h3 style={{color: 'white'}}>.בקוי עומד צוות שהגיע מעולם ההגברה והתאורה ובעל נסיון רב בתחום</h3>
                <h3 style={{color: 'white'}}>- המקום מתוגבר במערכת סאונד איכותית ביותר ויש מענה מבחינת הגברה ותאורה העונה לכל סוגי האירועים</h3>
                <h3 style={{color: 'white'}}>.בת/בר מצוות, אירועי חברה, נשפים, מסיבות גיוס/שחרור, הופעות חיות מסיבות וכל דבר שהוא סיבה למסיבה</h3>
                <h3 style={{color: 'white'}}>,המקום מעוצב ברמה הגבוהה ביותר וניתן לאבזר אותו איך שעולה ברוחכם ובהתאם לסוג האירוע</h3>
                <h3 style={{color: 'white'}}>- היחודיות שלנו היא הגמישות והחופש שיש במקום בהתאם לאופי האירוע</h3>
                <h3 style={{color: 'white'}}> .אירוע צהריים או ערב, ישיבה או עמידה. הכל אפשרי</h3>
                <h3 style={{color: 'white'}}>.יש רשיון עסק לאירועים ויש אישור מכירת/הגשת אלכוהול ומזון KOIל </h3>
                <h3 style={{color: 'white'}}>.המקום מכיל 300 איש שנעשה בשבילם את המיטב לחווית אירוח מושלמת</h3>
                <br />
                <br />
                <br />
            </div>
            <div style={{textAlign: 'center'}}>
                <h1 style={{color: 'white', fontFamily: 'Amatic SC cursive', fontSize: '50px'}}>גלריה</h1>
                <Carousel width="100%" autoPlay={true} infiniteLoop={true} showThumbs={false} useKeyboardArrows={true} thumbWidth="200px">
                    <div id="gallery" >
                        <img src="gallery1.jpg" style={{width: '50%', margin: 'auto'}} />
                    </div>
                    <div>
                        <img src="gallery2.jpg" style={{width: '50%', margin: 'auto'}}/>
                    </div>
                </Carousel>
            </div>
            <br />
            <br />
            <br />
            <div style={{textAlign: 'center'}}id="contact">
                <h1 style={{color: 'white', fontFamily: 'Amatic SC cursive', fontSize: '50px', paddingRight: '20px'}}>
                    צור קשר
                </h1>
                <h3  style={{color: 'white'}}>בואו לחוות מועדון מסוג שעוד לא הכרתם</h3>
                <br />
                <h2  style={{color: 'white'}}>0507742044</h2>
                <h2  style={{color: 'white'}}>דרך שלמה שמלצר 12 (אצטדיון המושבה) פתח תקווה</h2>
                <img src="facebook.png" className={classes.menuButton} style={{width: '40px', padding: '30px'}}/>
                <img src="instagram.png" className={classes.menuButton} style={{width: '40px', padding: '30px'}}/>
                <img src="whatsapp.png" className={classes.menuButton} style={{width: '40px', padding: '30px'}}/>

            </div>
            <br />
            <br />
            <br />
            <div className={classes.root}>
                <footer className={classes.footer}>
                    <Container maxWidth="sm">
                    <Copyright />
                    </Container>
                </footer>
            </div>
        </div>
    )
}